import React,{Component} from 'react';
import './Games.css';

import { ExternalLink } from 'react-external-link';

import imagejett from './Images/Jett_artwork.webp';
import imageghosty from './Images/ghosty.jpg';
import imageknife from './Images/knife.png';
import imageapple from './Images/apple.jpg';

import { Tabs, Tab, Grid, Cell, Card, CardTitle, CardText, Button } from 'react-mdl';


class Games extends Component {
    constructor(props) {
        super(props);
        this.state = { activeTab: 0 };
    }

    toggleCategories() {
            if(this.state.activeTab === 0){
                return(
                    <div>
                        <div className="projects-grid-1">
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imageapple} style={{maxWidth:'99%', maxHeight:'99%'}} alt="imagejett"/></CardTitle>
                                <CardText>ISAAC APPLE</CardText>
                                <Button colored>
                                    <ExternalLink href="https://not-akm.github.io/SphinX/Games/final_catchy_ver_%20webgl/index.html" style={{ textDecoration: 'none' }}>
                                    <span>Play Now</span>
                                    </ExternalLink>
                                </Button>
                                
                        </Card>

                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{color: '#fff', height: '176px', margin:'auto'}}><img src={imageknife} style={{maxWidth:'99%', maxHeight:'99%'}} alt="imagephoenix"/></CardTitle>
                                <CardText>KILLER KNIFE</CardText>
                                <Button colored>
                                    <ExternalLink href="https://not-akm.github.io/SphinX/Games/killer_knife_webgl_build/index.html" style={{ textDecoration: 'none' }}>
                                    <span>Play Now</span>
                                    </ExternalLink>
                                </Button>
                        </Card>

                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{color: '#fff', height: '176px', margin:'auto'}}><img src={imageghosty} style={{maxWidth:'99%', maxHeight:'99%'}} alt="imagereyna"/></CardTitle>
                                <CardText>RUSHY</CardText>
                                <Button colored>
                                    <ExternalLink href="https://not-akm.github.io/SphinX/Games/final_Rushy_webgl_build/index.html" style={{ textDecoration: 'none' }}>
                                    <span>Play Now</span>
                                    </ExternalLink>
                                </Button>
                        </Card>
                        </div>
                    </div>
                )
            }
            else if(this.state.activeTab === 1){
                return(
                    <div>
                    <div className="projects-grid-1">
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imagejett} style={{maxWidth:'80%'}} alt="imageviper"/></CardTitle>
                                <CardText>GAME</CardText>
                                <Button colored disabled>
                                    <ExternalLink href="//#endregion" style={{ textDecoration: 'none' }}>
                                    <span>Play Now</span>
                                    </ExternalLink>
                                </Button>
                        </Card>

                        </div>
                    </div>
                )
            }      
    }

    render() { 
        return (
        <div className="category-tabs">
            <Tabs activeTab={ this.state.activeTab } onChange={(tabID) => this.setState({ activeTab: tabID })} ripple>
                <Tab>Single Player</Tab>
                <Tab>Multi Player</Tab>
            </Tabs>

            <section className="allagentsbg">
                    <Grid>
                        <Cell col={12}><div className="content">{this.toggleCategories()}</div></Cell>
                    </Grid>             
            </section>
        </div>
        );
    }
}

export default Games;